html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #bd1522;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  

.container {
  width: 1270px;
}
.owl-carousel .owl-item img {
    width: auto;
}







.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #00000090;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #bd1522;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .zx-search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      border-radius: 50px;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px #bd152280;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    button {
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      left: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      [dir="ltr"] & {
        left: unset;
        right: 20px;
      }
      &:hover {
        color: #bd1522;
      }
    }
  }
}








/*------ upperbar start ------*/
.upper-bar {
  padding: 8px 0;
  background-color: #bd1522;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .upper-left, .upper-right {
    width: 270px;
    display: flex;
  }
  .upper-left {
    a {
      font-family: ar-reg;
      font-size: 14px;
      color: #ffffff;
      margin: 0 0 0 25px;
      transition: all .3s ease-in-out;
    }
  }
  .upper-right {
    justify-content: flex-end;
    align-items: center;
    font-family: ar-reg;
    font-size: 14px;
    color: #ffffff;
  }
  .upper-center {
    font-family: ar-bold;
    font-size: 16px;
    color: #ffffff;
  }
  .lang-switcher, .cur-switcher {
    position: relative;
    font-family: ar-reg;
    font-size: 14px;
    color: #fff;
    margin: 0 20px 0 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "\f0d7"; /* FontAwesome Unicode */
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 600;
        font-size: 11px;
        color: #fff;
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 55%;
      z-index: 9;
      transform: translateX(-50%);
      min-width: 110px;
      width: max-content;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 8px 5px;
      transition: all .5s ease-in-out;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      a {
        display: block;
        margin: 3px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #bd1522;
        }
      }
      span {
        padding: 0 2px;
      }
    }
    &:hover {
      > span {
        color: #fff;
        &::after {
          color: #fff;
        }
      }
      .switch {
        top: 23px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
/*------ upperbar end ------*/




/*------ navbar start ------*/
.nav-bar {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  z-index: 9;
