        font-family: ar-reg;
        font-size: 15px;
        color: #999;
      }
      &:hover, &:focus {
        border-color: #bd1522;
      }
    }
    button {
      font-size: 16px;
      color: #727071;
      position: absolute;
      top: 50%;
      left: 22px;
      transform: translateY(-50%);
      transition: all .3s ease-in-out;
      width: 16px;
      height: 16px;
      // width: 38px;
      // height: 38px;
      // border-radius: 50%;
      // background-color: #bd1522;
      // border: 1px solid #bd1522;
      &:hover {
        // background-color: #f5f5f5;
        color: #bd1522;
      }
      [dir="ltr"] & {
        left: unset;
        right: 22px;
      }
    }
  }
  .icons {
    width: 333px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .icon-container {
      position: relative;
      font-size: 18px;
      color: #727071;
      margin: 0 0 0 20px;  
      [dir="ltr"] & {
        margin: 0 20px 0 0;
      }  
      &:last-child {
        margin: 0;
      }
      a {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      i {
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
      span {
        font-family: ar-reg;
        font-size: 16px;
        transition: all .3s ease-in-out;
      }
      &:hover {
        color: #bd1522;
      }
    }
    .menu-icon, .search-icon {
      display: none;
    }
    .user-icon {
      position: relative;
      .after {
        cursor: pointer;
        transition: all .3s ease-in-out;
        &::after {
          content: "\f0d7"; /* FontAwesome Unicode */
          font-family: 'Font Awesome\ 5 Free';
          font-weight: 600;
          font-size: 11px;
          color: #727071;
          padding: 0 3px;
          transition: all .3s ease-in-out;
        }
      }
      .switch {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 40px;
        left: 55%;
        z-index: 9;
        transform: translateX(-50%);
        min-width: 130px;
        width: max-content;
        background-color: #fff;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
        color: #000;
        border-radius: 5px;
        padding: 8px 5px;
        transition: all .5s ease-in-out;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        a {
          display: block;
          margin: 3px 0;
          &:hover {
            color: #bd1522;
          }
        }
        span {
          padding: 0 2px;
        }
      }
      &:hover {
        > span {
          color: #fff;
          &::after {
            color: #fff;
          }
        }
        .switch {
          top: 30px;
          opacity: 1;
          visibility: visible;
        }
      }
    }
    .cart-icon {
      width: 50px;
      height: 50px;
      background-color: #727071;
      // border-style: solid;
      // border-width: 1px;
      // border-color: rgb(236, 236, 236);
      border-radius: 50%;
      color: #fff;
      font-size: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      .counter {
        position: absolute;
        top: -8px;
        right: -8px;
        width: 26px;
        height: 26px;
        padding: 1px 0 0 0;
        color: #ffffff;
        border-style: solid;
        border-width: 2px;
        border-color: rgb(255, 255, 255);
        border-radius: 50%;
        background-color: #bd1522;
        font-family: ar-reg;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
      }
      &:hover {
        color: #fff;
        background-color: #bd1522;
        .counter {
          transform: rotateY(360deg);
          background-color: #fff;
          border-color: #bd1522;
          color: #000;
        }
      }
    }
  }
  
}


