/*------ header start ------*/
.header {
  margin: 20px 0 0 0;
    .owl-carousel {
      position: relative;
      .owl-nav {
        .owl-prev, .owl-next {
          outline: none;
          width: 50px;
          height: 50px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 14px;
          background-color: #00000080;
          border-radius: 50%;
          color: #ffffff;
          transition: all .5s ease-in-out;
          &:hover {
            background-color: #bd1522;
          }
        }
        .owl-prev {
          right: 20px;
        }
        .owl-next {
          left: 20px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 15px;
          height: 5px;
          background-color: #00000080;
          border-radius: 50px;
          outline: none;
          margin: 0 2.5px;
          &.active {
            background-color: #fff;
          }
        }
      }
    }
   
}

/*------ header end ------*/




/*------ category start ------*/
.cats {
  margin: 0 0 40px 0;
  .cat {
    margin: 0 auto;
    position: relative;
    transition: all .5s ease-in-out;
    &:hover {
      .cat-title {
        color: #bd1522;
      }
    }
  }
  .cat-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: ar-reg;
    font-size: 20px;
    color: #1a1a1a;
    margin: 20px 0 0 0;
    transition: all .3s ease-in-out;
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: none;
    }
    .owl-dots {
      display: none;
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: -30px;
      transform: translateX(-50%);
      .owl-dot {
        width: 15px;
        height: 5px;
        background-color: #00000080;
        border-radius: 50px;
        outline: none;
        margin: 0 2.5px;
        &.active {
          background-color: #bd1522;
        }
      }
    }
  }
}
