/*--- card start ---*/
.card {
  width: 212px;
  // min-height: 220px;
  overflow: hidden;
  margin: 0 auto;
  &:hover {
    img {
      transform: scale(1.05);
    }
  }
  .card-img {
    height: 220px;
    background-color: #f7f7f7;
    margin: 0 0 0px 0;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      transition: all .5s ease-in-out;
    }
  }
  .card-body {
    padding: 0 5px;
    .sp-offer-button {
      button {
        border-radius: 50px;
        background-color: #bd1522;
        width: 204px;
        height: 34px;
        font-family: ar-bold;
        font-size: 16px;
        color: #ffffff;
        margin: 10px 0 15px 0;
      }
    }
    .card-option {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .amount {
        width: 120px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        button {
          width: 40px;
          height: 40px;
          border-style: solid;
          border-width: 1px;
          border-color: #d7d7d7;
          border-radius: 50%;
          background-color: #ffffff;
          color: #a6a2a4;
          font-size: 14px;
          transition: all .3s ease-in-out;
          &:hover {
            color: #bd1522;
            border-color: #bd1522;
          }
        }
        input {
          width: 40px;
          display: flex;
          justify-content: center;
          align-items: center;
          border: none;
          outline: none;
          text-align: center;
          &::placeholder {
            font-family: ar-reg;
            font-size: 15px;
            color: #1a1a1a;
            text-align: center;
          }
        }
      }
      .card-icon {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background-color: #727071;
        font-size: 14px;
        color: rgb(255, 255, 255);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .3s ease-in-out;
        &:hover {
          background-color: #bd1522;
        }
      }
    }
  }
  .title {
    font-family: ar-reg;
    font-size: 15px;
    color: #333;
    margin: 20px 0 5px 0;
    padding: 0 0 5px 0;
    height: 47px;
    overflow: hidden;
    text-align: center;
  }
  .price {
    font-family: ar-bold;
    font-size: 18px;
    color: #cc0033;
    margin: 0 0 15px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 0 5px 0;
    .old {
      font-family: ar-reg;
      font-size: 16px;
      color: #999;
      text-decoration: line-through;
      margin: 0 0px;
    }
  }
}

